import React from 'react';
import './Comments.sass'
import axios from 'axios';
import _ from "lodash";

class Comments extends React.Component {
  constructor(props) {
	 super(props);
	 this.state = {
		data: '',
	 }
  }

  componentDidMount() {
	 axios.get(`http://localhost:8888/comments.php?id=${this.props.id}`)
		.then(response => {
		  this.setState({
			 data: response.data
		  });
		})
		.catch(error => {
		  console.log(error)
		});
  }
  handleInputChange = (e) =>  {
	 this.setState({state: this.state.errors.email = ''});
	 this.setState({state: this.state.errors.password = ''});
	 const value = e.target.type === 'checkbox' ? e.target.checked : e.target.value;
	 const name = e.target.name;
	 this.setState({
		[name]: value
	 });
  };
  add = () => {

  };

  delete = (id) => {
	 axios.delete(`http://localhost:8888/comments.php?id=${id}&idw=${this.props.id}`)
		.then(response => {
		  if (response) {
			 _.remove(this.state.data, friend => friend.id === id);
			 this.setState({
				data: this.state.data
			 })
		  }
		})
		.catch(error => {
		  console.log(error)
		});
  };

  render() {
	 return (
		<>
		  <h3>Коментарі</h3>
		  <form method="POST">
			 <input
				type="text"
				name="name"
				placeholder="Ім'я"
				value={this.state.value}
				onChange={this.handleInputChange}
			 /><br />
			 {/*<span>{this.state.errors.email ? this.state.errors.email : null}</span>*/}
			 <input
				type="text"
				name="text"
				placeholder="Коментар"
				value={this.state.value}
				onChange={this.handleInputChange}
			 /><br />
			 {/*<span>{this.state.errors.password ? this.state.errors.password : null}</span>*/}

			 <button onClick={this.add}>Відправити</button>
		  </form>
		  {this.state.data != '' ?
			 Object.keys(this.state.data).map((item, i) => (
				<div key={i} className="content__items--item">
				  <div>{this.state.data[item].name}</div>
				  <div>{this.state.data[item].text}</div>
				  <div>{this.state.data[item].date}</div>
				  <br/>
				  <span
					 onClick={() => this.delete(this.state.data[item].id)}>X - {this.state.data[item].id}</span><br/><br/><br/><br/>
				</div>
			 ))
			 :
			 <div>Будьте першими хто залишить коментар :)</div>
		  }
		</>
	 );
  }
}

export default Comments;